
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'Details',
components: {
XHeader
},
methods: {
back: function () {
this.$router.go(-1)
}
},
mounted: function () {
this.$vux.toast.show({
text: this.$route.params.id,
type: 'text'
})
setTimeout(() => {
this.$vux.toast.hide()
}, 2000)
}
}



